"use client";
import ProtectedRoute from '../../components/ProtectedRoute';
import { useAuth } from '../../context/authContext';
import { UserCircleIcon, BookOpenIcon, CalendarIcon, UsersIcon, ClipboardListIcon, AcademicCapIcon, ChartBarIcon, SpeakerphoneIcon, Cog6ToothIcon, QuestionMarkCircleIcon } from '@heroicons/react/24/outline';

const metrics = [
  { label: 'Usuarios Activos', value: '1,250' },
  { label: 'Grados Registrados', value: '60' },
  { label: 'Anuncios Recientes', value: '15' },
];

const activity = [
  { user: 'Isabella Rodriguez', action: 'Creó un nuevo usuario: "Juan Perez"', date: '2024-07-26' },
  { user: 'Mateo Vargas', action: 'Actualizó la información del grado "5° A"', date: '2024-07-25' },
  { user: 'Sofía Herrera', action: 'Eliminó la asignatura "Historia"', date: '2024-07-24' },
  { user: 'Diego Castro', action: 'Publicó un nuevo anuncio general', date: '2024-07-23' },
  { user: 'Lucía Mendoza', action: 'Generó un reporte de matrículas', date: '2024-07-22' },
];

const sidebarLinks = [
  { label: 'Gestión de Usuarios', icon: UsersIcon },
  { label: 'Grados/Secciones', icon: AcademicCapIcon },
  { label: 'Asignaturas', icon: BookOpenIcon },
  { label: 'Horarios', icon: CalendarIcon },
  { label: 'Matrícula', icon: ClipboardListIcon },
  { label: 'Ciclos Lectivos', icon: CalendarIcon },
  { label: 'Reportes', icon: ChartBarIcon },
  { label: 'Anuncios', icon: SpeakerphoneIcon },
  { label: 'Configuración', icon: Cog6ToothIcon },
  { label: 'Ayuda/FAQs', icon: QuestionMarkCircleIcon },
];

export default function AdminDashboardPage() {
  const { logout } = useAuth();
  return (
    <ProtectedRoute allowedRoles={["ADMIN"]}>
      <div className="flex min-h-screen bg-gray-900 text-white">
        {/* Sidebar */}
        <aside className="w-64 bg-gray-950 flex flex-col p-6 border-r border-gray-800">
          <div className="flex items-center mb-10">
            <img src="https://randomuser.me/api/portraits/women/44.jpg" alt="Admin" className="w-12 h-12 rounded-full mr-3 border-2 border-green-400" />
            <span className="text-2xl font-bold tracking-tight">AulaUnida</span>
          </div>
          <nav className="flex-1">
            <ul className="space-y-2">
              {sidebarLinks.map((link, idx) => (
                <li key={link.label}>
                  <a href="#" className={`flex items-center px-4 py-2 rounded-lg transition-colors ${idx === 0 ? 'bg-green-600 text-white' : 'hover:bg-gray-800'}`}>
                    <link.icon className="w-5 h-5 mr-3" />
                    {link.label}
                  </a>
                </li>
              ))}
            </ul>
          </nav>
          <button onClick={logout} className="mt-8 bg-red-500 hover:bg-red-600 text-white py-2 rounded-lg font-semibold transition-colors">Cerrar sesión</button>
        </aside>

        {/* Main content */}
        <main className="flex-1 p-10 bg-gray-900">
          <h1 className="text-4xl font-extrabold mb-2">Panel de Administrador</h1>
          <p className="text-gray-300 mb-8">Bienvenido, aquí tienes un resumen del estado del sistema.</p>

          {/* Métricas */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-10">
            {metrics.map((m) => (
              <div key={m.label} className="bg-gray-800 rounded-xl p-8 flex flex-col items-center shadow-lg">
                <span className="text-lg text-gray-400 mb-2">{m.label}</span>
                <span className="text-4xl font-bold text-green-400">{m.value}</span>
              </div>
            ))}
          </div>

          {/* Actividad reciente */}
          <div className="bg-gray-800 rounded-xl p-8 shadow-lg">
            <h2 className="text-2xl font-bold mb-6">Actividad Reciente</h2>
            <div className="overflow-x-auto">
              <table className="min-w-full text-left">
                <thead>
                  <tr className="text-gray-400 border-b border-gray-700">
                    <th className="py-2 px-4">USUARIO</th>
                    <th className="py-2 px-4">ACCIÓN</th>
                    <th className="py-2 px-4">FECHA</th>
                  </tr>
                </thead>
                <tbody>
                  {activity.map((a, idx) => (
                    <tr key={idx} className="border-b border-gray-800 hover:bg-gray-700/30">
                      <td className="py-2 px-4 font-semibold">{a.user}</td>
                      <td className="py-2 px-4">{a.action}</td>
                      <td className="py-2 px-4">{a.date}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </main>
      </div>
    </ProtectedRoute>
  );
}
